import React, { Component } from 'react';
import { CollapseButton } from './Utils.js';
import withCollapse from './withCollapse.js';

const TXC = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Buy TXC
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h3> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <p className="lead">TXC is the subscription badge of TXCast. Every posted transaction charges a 30% fee of the reward. Addresses with at least 1 TXC do not pay this fee. Subscriptions can be transferred to other addresses and never expire.</p>
      <p className="small">There will only be a total of 5000 TXC subscriptions, and their price is fixed at 1 TXC = 1 ETH for the duration of the sale. TXC isn't subdivisible, so the minimum amount for sale is 1 TXC. However, an address can buy as many whole units of TXC as it wishes. After the 5000 subscriptions are sold, new users will have buy the subscriptions from previous owners.</p>
      <div className="form-group">
        <button
          type="button"
          onClick={() => props.onBuy()}
          className="btn btn-success btn-lg btn-block"
        >
          Buy TXC
        </button>
      </div>
    </div>
  </div>
);

class TXCContainer extends Component {
  render() {
    return(
      <TXC
        {...this.props}
      />
    );  
  }
}

export default withCollapse(TXCContainer);
