import React, { Component } from 'react';
import Toggle from 'react-toggle';
import { CollapseButton, DropdownAppend } from './Utils.js';
import withCollapse from './withCollapse.js';

const Deposit = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Ether and Token Deposits
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
        <i className="ml-3 small fas fa-question-circle"></i>
      </h3> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <DepositInput
        {...props}
      />
    </div>
  </div>
);

const DepositInput = props => (
  <form>
    <div className="form-group">
      <label>Beneficiary's Address</label>
      <i data-tip="Until release, the beneficiary is the unlocked wallet." className="fas fa-info-circle ml-2"></i>

      <div className="input-group">
        <input
          disabled
          className="form-control text-monospace bg-primary"
          id="beneficiaryAddress"
          value={props.address}
        />
      </div>
      <small id="raHelp" className="form-text text-gray">Address of the beneficiary of your deposit.</small>
    </div>
    <div className="form-group">
      <label>Amount</label>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({...props.inputData, amount: e.target.value})}
          className="form-control text-monospace"
          id="depositAmount"
          value={props.inputData.amount}
        />
        <DropdownAppend symbols={props.symbols}/>
      </div>
      <small id="aHelp" className="form-text text-gray">Amount to deposit to the beneficiary.</small>
    </div>

    <button
      onClick={(e) => props.onDeposit(e)}
      className="btn btn-block btn-lg btn-success"
    >
      Deposit 
    </button>
  </form>
);

const selfdeposit = (props) => (
  <label className="switch">
    <input
      type="checkbox"
      checked={props.selfDepositToggle}
      value={props.selfDepositToggle}
      onChange={() => props.toggleSelfDeposit()}
    />
    <div className="slider ml-2"></div>
  </label>  
);

class DepositContainer extends Component {
  constructor(props){
    super(props);
    this.state = {amount: 0, selfDepositToggle: true};
  }

  render() {
    return (
      <Deposit
        {...this.props}
        onInputChange={this.onInputChange.bind(this)}
        onDeposit={this.onDeposit.bind(this)}
        toggleSelfDeposit={this.toggleSelfDeposit.bind(this)}
        inputData={this.state}
        symbols={['ETH','ZRX','DAI','REP']}
      />
    );
  }

  onDeposit(e) {
    e.preventDefault();
    this.props.onDeposit(this.state);
  }

  toggleSelfDeposit() {
    this.setState({...this.state, selfDepositToggle: !this.state.selfDepositToggle}); 
  }

  onInputChange(newState) {
    if(/(^[0-9]+\.?[0-9]{0,18}$)|(^$)/.test(newState.amount))
      this.setState(newState);
  }
}

export default withCollapse(DepositContainer);
