import React from 'react';
import { TXCStatus } from './Utils.js';
import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem } from 'reactstrap';

class NavBar extends React.Component {
  constructor(props) {
    super(props);

    this.toggle = this.toggle.bind(this);
    this.state = {
      isOpen: false
    };
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen
    });
  }
  render() {
    const hasWeb3 = this.props.addressList[0] != undefined;

    return (
      <div>
        <Navbar className="text-monospace text-black" color="#FFF" light expand="md">
          <img src={this.props.logo} width="30" height="30" className="d-inline-block align-top mx-3" alt="txcast logo"/>
          <NavbarBrand className="font-weight-bold" style={{"letterSpacing":"7px"}}>
            TXCast
          </NavbarBrand>
          <NavbarToggler onClick={this.toggle} />
          <Collapse isOpen={this.state.isOpen} navbar>
            <Nav className="ml-auto" navbar>
              <NavItem className="mr-3">
                <NavLink
                  data-tip="Under construction"
                  className="text-success"
                  href="#"
                >
                  Help
                </NavLink>
              </NavItem>
              <NavItem className="mr-3">
                <TXCStatus active={this.props.hasTXC ? true : false} />
              </NavItem>
              <UncontrolledDropdown nav inNavbar>
                <DropdownToggle nav caret>
                  Address and Blocks
                </DropdownToggle>
                <DropdownMenu right>
                  <DropdownItem disabled>
                    <small>
                      <i style={{"color": this.props.netid === 1 ? "teal": "purple"}} className="fas fa-wifi mr-2"></i>
                      Active TXCast Address: <a
                        href={this.props.ethsUrl}
                        className="text-monospace">
                        {this.props.ethsAddress}
                      </a>
                    </small>
                  </DropdownItem>
                  {this.props.addressList[0] && <AddressItem {...this.props}/>}
                  <DropdownItem style={{"borderTopColor":"gray"}} divider/>
                  <DropdownItem disabled>
                    <small className="float-left">
                      <i className={"fa" + (hasWeb3 ? "s" : "r") + " fa-circle text-" + (hasWeb3 ? "success" : "info")}></i> web3 {hasWeb3 ? "enabled" : "disabled"}
                    </small>
                    <small className="float-right">
                      Current block: <span className="text-monospace">{this.props.blockNumber}</span>
                    </small>
                  </DropdownItem>
                </DropdownMenu>
              </UncontrolledDropdown>
            </Nav>
          </Collapse>
        </Navbar>
      </div>
    );
  }
}

const AddressItem = (props) => (
  <DropdownItem disabled>
    <small className="float-right">
      Your address: <a
        href={props.coinbaseUrl}
        className="text-monospace">
        {props.addressList[0]}
      </a>
    </small>
  </DropdownItem>
);
export default NavBar;
