import React, { Component } from 'react';
import { CollapseButton, DropdownAppend } from './Utils.js';
import withCollapse from './withCollapse.js';

const SendRaw = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Broadcast TX
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h3>
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <SendRawInput
        {...props}
      />
    </div>
  </div>
);

const SendRawInput = props => (
  <form>
    <div className="form-group">
      <label>TX Data</label>
      <i data-tip="These data can be copied to the clipboard from the TX Queue sub-app." className="fas fa-info-circle ml-2"></i>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({callDataTX: JSON.parse(e.target.value)})}
          className="form-control text-monospace"
          id="address"
          value={JSON.stringify(props.inputData.callDataTX)}
        />
        <div className="input-group-append">
          <button
            onClick={(e) => props.onSendRaw(e)}
            className="btn btn-primary"
          >
           Broadcast TX 
          </button>
        </div>
        <small id="raHelp" className="form-text text-gray"></small>
      </div>
    </div>
  </form>
);

class SendRawContainer extends Component {
  constructor(props){
    super(props);
    this.state = {callDataTX: {}};
  }

  render() {
    return (
      <SendRaw
        {...this.props}
        onInputChange={this.onInputChange.bind(this)}
        onSendRaw={this.onSendRaw.bind(this)}
        inputData={this.state}
        symbols={['ETH','ZRX','DAI','REP']}
      />
    );
  }

  onSendRaw(e) {
    e.preventDefault();
    this.props.onSendRaw(this.state);
  }

  onInputChange(newState) {
    this.setState(newState);
  }
}

export default withCollapse(SendRawContainer);
