import React, { Component } from 'react';

class DropdownAppend extends Component {
  constructor(props){
    super(props);

    this.state = {toggle: false, filter: ""};
  }

  setFilter(v) {
    this.setState({...this.state, filter: v});
  }

  toggleList() {
    this.setState(prevState => ({
      toggle : !prevState.toggle
    }))
  }

  render() {
    return ( 
      <div className="input-group-append">
        <button
          disabled={this.props.disabled}
          className={"btn btn-primary dropdown-toggle" }
          onClick={() => this.toggleList()}
          type="button"
          data-toggle="dropdown"
        >
          ETH
        </button>
        <div className={"dropdown-menu dropdown-menu-right " + (this.state.toggle ? "show" : "")}>
          <input
            className="filter-input pl-2 m-1"
            onChange={(e) => this.setFilter(e.target.value)}
            type="text"
            placeholder="Filter"
            id="ticker"
          />
          {this.props.symbols
              .filter(w =>
                w.toUpperCase().search(this.state.filter.toUpperCase()) > -1)
              .map(v => 
                <a key={v} className="dropdown-item" href="#">{v}</a>)}
            </div>
          </div>
    )
  }
}

