import React, { Component } from 'react';
import { CollapseButton, DropdownAppend } from './Utils.js';
import withCollapse from './withCollapse.js';

const Scheduler = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Post Scheduled TX
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
        <i className="ml-3 small fas fa-question-circle"></i>
      </h3> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <ScheduleInput
        getTime={props.getTime}
        onInputChange={props.onInputChange}
        onSign={props.onSign}
        inputData={props.inputData}
        symbols={props.symbols}
      />
    </div>
  </div>
);

const ScheduleInput = props => (
  <form>
    <div className="form-group">
      <label>Receiver Address</label>
        <input
          onChange={(e) => props.onInputChange({...props.inputData, address: e.target.value})}
          className="form-control text-monospace"
          id="recieverAddress"
          value={props.inputData.address}
       />
      <small id="raHelp" className="form-text text-gray">Address of the end-recipient of your transaction.</small>
    </div>
    <div className="form-group">
      <label>Amount</label>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({...props.inputData, amount: e.target.value})}
          className="form-control text-monospace"
          id="amount"
          value={props.inputData.amount}
       />
        <DropdownAppend disabled symbols={props.symbols}/>
      </div>
      <small id="aHelp" className="form-text text-gray">Amount to send to the receiver address.</small>
    </div>
    <div className="form-group">
      <label>Reward</label>
      <i data-tip="The reward implicitly includes the network transaction fee." className="fas fa-info-circle ml-2"></i>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({...props.inputData, reward: e.target.value})}
          className="form-control text-monospace"
          id="reward"
          value={props.inputData.reward}
       />
        <DropdownAppend disabled symbols={props.symbols}/>
      </div>
      <small id="rwHelp" className="form-text text-gray">Amount awarded to the address that fulfills your request.</small>
    </div>
    <div className="form-group">
      <label>Blocks</label>
      <i data-tip="Blocks till your TX is available for mining. One block is mined every ~15 seconds." className="fas fa-info-circle ml-2"></i>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({...props.inputData, blocks: e.target.value})}
          className="form-control text-monospace"
          id="blocks"
          value={props.inputData.blocks}
       />
        <div className="input-group-append">
          <div style={{"cursor":"help"}} data-tip={props.getTime(props.inputData.blocks)} className="btn btn-primary disabled">Blocks</div>
        </div>
      </div>
      <small id="bHelp" className="form-text text-gray">Blocks to wait before the transaction is broadcasted.</small>
    </div>

    <button
      onClick={(e) => props.onSign(e)}
      className="btn btn-block btn-lg btn-success"
    >
      Sign and Schedule
    </button>
  </form>
);


class SchedulerContainer extends Component {
  constructor(props){
    super(props);
    this.state = {address: '0x0000000000000000000000000000000000000000', amount: 0, reward: 0, blocks: 10};
  }

  getTime(blocks) {
    const time = blocks*15/3600;
    if(time < 2)
      return "Your input is equivalent to ~" + (time*60).toFixed(2) + " minutes.";
    else
      return "Your input is equivalent to ~" + time.toFixed(2) + " hours.";
  }
  
  render() {
    return (
      <Scheduler
        {...this.props}
        getTime={this.getTime.bind(this)}
        onInputChange={this.onInputChange.bind(this)}
        onSign={this.onSign.bind(this)}
        inputData={this.state}
        symbols={['ETH','ZRX','DAI','REP']}
      />
    );
  }

  onSign(e) {
    e.preventDefault();
    this.props.onSign(this.state);
  }

  onInputChange(newState) {
    if((/(^[0-9][0-9]{0,5}$)|(^$)/.test(newState.blocks))
      && (/(^[0-9]+\.?[0-9]{0,18}$)|(^$)/.test(newState.reward))
      && (/(^[0-9]+\.?[0-9]{0,18}$)|(^$)/.test(newState.amount))
      && (/^0x[0-9a-f]{40}$/i.test(newState.address)))
        this.setState(newState);
  }
}

export default withCollapse(SchedulerContainer);
