import React, { Component } from 'react';
import { CollapseButton, ToggleBlocksButton } from './Utils.js';
import { CopyToClipboard } from 'react-copy-to-clipboard';
import withCollapse from './withCollapse.js';

const TXQueue = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h2 className="text-monospace font-weight-bold">
        TX Queue
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
        <ToggleBlocksButton
          onToggleBlocks={props.onToggleBlocks}
          blocksFormat={props.blocksFormat}
        />
        <i className="ml-3 small fas fa-question-circle"></i>
      </h2> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <div className="table-container">
        <table className="table table-hover">
          <thead className="bg-white text-monospace">
            <tr className="text-black d-flex" style={{'height':'30px'}}>
              <th scope="col" className="col-5">Requester</th>
              <th scope="col" className="col-3 text-center">
                <i className={"fas fa-" + (props.blocksFormat ? "cube" : "calendar")}></i> <span>Schedule</span>
              </th>
              <th scope="col" className="col-4 text-center">Reward</th>
            </tr>
          </thead>
          <tbody className="text-black text-monospace">
            {
              props.posts.map(p =>
                <TXReady
                  {...p}
                  account={props.account}
                  onCancel={props.onCancel}
                  onFulfill={props.onFulfill}
                  fromWei={props.fromWei}
                />
              )
            } {
              props.requests.map(p =>
                <TXPost
                  {...p}
                  account={props.account}
                  blocksFormat={props.blocksFormat}
                  account={props.account}
                  onCancel={props.onCancel}
                  fromWei={props.fromWei}
                />
              )
            }
          </tbody>
        </table>  
      </div>
    </div>
  </div>
);

const TXReady = props => (
  <tr className="table-light text-black d-flex hoverHide">
    <td className="col-5 pl-3">
      <span className="sendSpan">{props.signer}</span>
      <button
        className="btn btn-sm btn-success btn-block table-button"
        onClick={() => props.onFulfill(props)}
      >
        <i class="fas fa-paper-plane"></i> Send this TX 
      </button>
    </td>
    <td className="col-3 text-center">Ready</td>
    <td className="col-4 text-center">
      {(props.account === props.signer) ? <CancelButton {...props}/> : props.fromWei(props.reward,'ether') + " ETH"}
    </td>
  </tr>
);

const TXPost = props => (
  <tr className="table-secondary text-black d-flex hoverHide">
    <td className="col-5 pl-3">
      <span className="sendSpan">{props.signer}</span>
      <CopyToClipboard text={JSON.stringify(props)}>
        <button
          className="btn btn-sm btn-danger btn-block table-button"
          onClick={(e) => (0)}
        >
          <i class="fas fa-clipboard"></i> Copy Signature 
        </button>
      </CopyToClipboard>
    </td>
    <td className="col-3 text-center">
      <TimeDisplay {...props}/>
    </td>
    <td className="col-4 text-center">
      {(props.account === props.signer) ? <CancelButton {...props}/> : props.fromWei(props.reward,'ether') + " ETH"}
    </td>
  </tr>
);

const CancelButton = (props) => (
  <span>
    <span className="sendSpan">{props.fromWei(props.reward,'ether')} {"ETH"}</span>
    <button
      className="btn btn-sm btn-danger btn-block table-button"
      onClick={() => props.onCancel(props.sig, props.nonce)}
    >
      <i class="fas fa-ban"></i> Cancel TX
    </button>
  </span>
);


const TimeDisplay = (props) => (<span>{props.blocksFormat ? props.schedule: props.timestampDisplay}</span>);


class TXQueueContainer extends Component {
  constructor(props) {
    super();

    this.state = {
      blocksFormat: true,
    }
  }

  componentDidMount() {
    this.props.fetchData();
  }

  onToggleBlocks() {
    this.setState({blocksFormat: !this.state.blocksFormat});
    console.log(this.state)
  }

  render() {
    const data      = Object.values(this.props.queue);
    const sigs      = Object.keys(this.props.queue);
    const zipped    = data.forEach((p,i) => p.sig = sigs[i]) || data;
    const posts     = this.state.blocksFormat ?
      zipped :
      zipped.forEach(p => {
        p.timestampDisplay = new Date(Date.now() + 15*(p.schedule- this.props.blockNumber)).toISOString().replace("T"," ").split(":").slice(0,2);
        p.timestamp        = new Date(Date.now() + 15*(p.schedule- this.props.blockNumber)).toISOString();
      }) || zipped;

    console.log("txq", zipped)

    const pre   = posts.filter(p => p.schedule < this.props.blockNumber);
    const ready = posts.filter(p => p.schedule >= this.props.blockNumber);
    
    return (
      <TXQueue
        {...this.props}
        posts={pre}
        requests={ready}
        blocksFormat={this.state.blocksFormat}
        onCancel={this.props.onCancel}
        onToggleBlocks={this.onToggleBlocks.bind(this)}
        onFulfill={this.props.onFulfill}
        fromWei={this.props.fromWei}
      />  
    );
  }
}

export default withCollapse(TXQueueContainer);
