import React, { Component } from 'react';
import { CollapseButton } from './Utils.js';
import withCollapse from './withCollapse.js';

const Notice = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Notices
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h3> 
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <p>TXCast is in beta release. Proceed with caution and make sure to read what each sub-app does.</p>
    </div>
  </div>
);

class NoticesContainer extends Component {
  render() {
    return (
      <Notice {...this.props}/>
    )
  }
}

export default withCollapse(NoticesContainer);
