import React, { Component } from 'react';
import { CollapseButton, DropdownAppend } from './Utils.js';
import withCollapse from './withCollapse.js';

const Balance = props => (
  <div className="card text-white mb-3">
    <div className="card-header">
      <h3 className="text-monospace">
        Balances
        <CollapseButton
          onCollapse={props.onCollapse}
          collapsed={props.collapsed}
        />
      </h3>
    </div>  
    <div className={"card-body" + (props.collapsed ? " card-collapse" : "")}>
      <BalanceInput
        {...props}
      />
    </div>
  </div>
);

const BalanceInput = props => (
  <form>
    <div className="form-group">
      <label>Address</label>
      <div className="input-group">
        <input
          onChange={(e) => props.onInputChange({...props.inputData, address: e.target.value})}
          className="form-control text-monospace"
          id="address"
          value={props.inputData.address}
        />
        <div className="input-group-append">
          <button
            onClick={(e) => props.onBalance(e)}
            className="btn btn-primary"
          >
            Check
          </button>
        </div>
        <small id="raHelp" className="form-text text-gray"></small>
      </div>
    </div>
    <div className="form-group">
      <h3>ETH: <span className="float-right">{props.balance}</span></h3>
    </div>
  </form>
);

class BalanceContainer extends Component {
  constructor(props){
    super(props);
    this.state = {address: '0x0000000000000000000000000000000000000000', amount: 0};
  }

  render() {
    return (
      <Balance
        {...this.props}
        onInputChange={this.onInputChange.bind(this)}
        onBalance={this.onBalance.bind(this)}
        inputData={this.state}
        symbols={['ETH','ZRX','DAI','REP']}
      />
    );
  }

  onBalance(e) {
    e.preventDefault();
    this.props.onBalance(this.state);
  }

  onInputChange(newState) {
    if(/^0x[0-9a-f]{40}$/i.test(newState.address))
      this.setState(newState);
  }
}

export default withCollapse(BalanceContainer);
